@import './helpers//Colors.scss';
@import './helpers/HoverSupported.scss';

.container-projet {
  background-color: $main-yellow;
  overflow: hidden;

  img {
    width: 100%;
    height: 250px;
    object-fit: cover;
    filter: blur(0) grayscale(0);
    transition: all 0.5s ease-in-out;
  }

  @include hover-supported() {
    img {
      transform: scale(1.1, 1.1);
      filter: blur(2px) grayscale(1);
    }
  }

  .caption {
    height: 250px;
    width: 100%;
    z-index: 1;
    position: absolute;
    top: 0;
    opacity: 0;
    transition: all 0.45s ease-in-out;
    cursor: pointer;

    @include hover-supported() {
      opacity: 1;
    }


    .blur {
      z-index: 2;
      position: absolute;
      background-color: rgba($main-grey, 0.65);
      height: 250px;
      width: 100%;
    }

    .caption-text {
      z-index: 3;
      position: relative;
      width: 100%;
      height: 250px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;

      .caption-title {
        margin: 0;
        font-size: 26px;
        font-weight: 600;
        color: $main-yellow;
        text-transform: uppercase;
      }

      .caption-location {
        font-size: 16px;
        font-weight: 500;
        font-style: italic;
      }

      // .caption-label-stack {
      //   display: flex;
      //   flex-direction: row;

      //   p {
      //     background-color: #FFF;
      //     font-size: 12px;
      //     font-weight: 300;
      //     color: $main-grey;
      //     padding: 3px 8px 3px 8px;
      //   }
      // }
    }

    .btn-see-project {
      background-color: transparent;
      border: solid 1px $main-yellow;
      padding: 5px 8px;
      color: $main-yellow;
      font-size: 14px;
      font-weight: 400;
      text-transform: uppercase;

      &:hover {
        color: $main-grey;
      }
    }
  }
}